import axios from 'axios';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:3001/';
console.log(API_URL);

const axiosInstance = axios.create({
  headers: {
    'Content-Type': 'application/json',
  }
});

const axiosFilesInstance = axios.create({
  headers: {
    'Content-Type': 'multipart/form-data',
  }
});

export function getSets() {
  return axiosInstance.get(API_URL + 'admin/sets');
}

export function postSet(data) {
  return axiosInstance.post(API_URL + 'admin/sets', data);
}

export function deleteSet(name) {
  return axiosInstance.delete(API_URL + 'admin/sets/' + name);
}

export function getLayers(setName) {
  return axiosInstance.get(API_URL + 'admin/' + setName + '/layers');
}

export function deleteLayer(setName, id) {
  return axiosInstance.delete(API_URL + 'admin/' + setName + '/layers/' + id);
}

export function getLayerItems(setName, id) {
  return axiosInstance.get(API_URL + 'admin/' + setName + '/layers/' + id);
}

export function postLayerItemsImages(setName, id, fileToSend) {
  let data = new FormData();
  data.append('image', fileToSend);
  data.append('name', 'image');

  return axiosFilesInstance.post(API_URL + 'admin/' + setName + '/layers/' + id + '/images', data);
}


export function deleteLayerItemImage(setName, id, imageId) {
  return axiosInstance.delete(API_URL + 'admin/' + setName + '/layers/' + id + '/images/' + imageId);
}


export function patchLayerItem(setName, id, data) {
  return axiosInstance.patch(API_URL + 'admin/' + setName + '/layers/' + id, data);
}


export function postLayerItemColor(setName, id, color) {
  return axiosInstance.post(API_URL + 'admin/' + setName + '/layers/' + id + '/colors', {color});
}
//props.match.params.setName


export function deleteLayerItemColor(setName, id, color) {
  color = color.replace('#', '');
  return axiosInstance.delete(API_URL + 'admin/' + setName + '/layers/' + id + '/colors/' + color);
}

export function addLayer(setName, data) {
  return axiosInstance.post(API_URL + 'admin/' + setName + '/layers/', data);
}

